
@font-face {

	font-family: 'Frutiger-Light';
	src: url('/fonts/frutiger-lightnrtr-webfont.woff2') format('woff2'),
		 url('/fonts/frutiger-lightnrtr-webfont.woff') format('woff');
}

@font-face {
	font-family: 'Frutiger-Bold';
	src: url('/fonts/frutiger-boldtr-webfont.woff2') format('woff2'),
		 url('/fonts/frutiger-boldtr-webfont.woff') format('woff');
}


@mixin lg {	

	@media (min-width: 1200px) { 

		@content;
	}
}

@media (max-width: 1199px) {

	.show-desktop {

		display: none !important;
	}
}

@media (min-width: 1200px) {

	.show-mobile {

		display: none !important;
	}
}


*, *:before, *:after {

	box-sizing: inherit;
}

html {

	box-sizing: border-box;
}

body {

	max-width: 500px;

	margin: 0 auto;

	font-family: "Frutiger-Light", Arial;
	font-size: 17px;
	line-height: 1.5em;

	color: #333;

	@include lg {

		max-width: none;

		font-size: 20px;
	}
}

p {

	margin: 14px 0;
}

a {

	color: black;
	text-decoration: none !important;
}

img {

	max-width: 100%;
}

.is-bold {

	font-family: "Frutiger-Bold", Arial;
}

.is-large {

	font-size: 32px;
	line-height: 1em;
}

.clearfix:after {

	content: "";
	display: table;
	clear: both;
}

header {

	img {

		display: block;

		width: 100%;
		max-width: 1920px;

		margin: auto;
	}
}

main {

	article {

		width: calc(100% - 40px);
		margin: auto;

		@include lg {

			width: 1200px;
		}

		div.block {

			position: relative;
			float: left;

			padding: 28px;
			margin-bottom: 15px;

			border: 1px solid #c6cbdd;
			background-color: #dcdee3;

			div.info {


				@include lg {

					width: 90%;

					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
				}

				p {

					margin: 10px 0;
					font-size: 18px;
					line-height: 1.25em;
				}

				a {

					font-weight: 700;
				}
			}
		}

		div.wide {

			width: 100%;

			margin-top: 20px;

			text-align: center;

			background-image: url("../images/huawei-bg.png");

			@include lg {

				height: 320px;
			}

			span {

				color: #cc0000;
			}
		}

		div.narrow {

			width: 100%;

			@include lg {

				width: 390px;
				height: 290px;
			}

			p {

				margin: 8px 0;
				font-size: 17px;
				line-height: 1.25em;
			}
		}

		div.content {

			@include lg {

				position: absolute;
				bottom: 0; left: 0;

				padding: 20px
			}

			p.ardess {

				font-size: 19px;

				@include lg {

					font-size: 24px;
				}
			}
		}

		div.n-center { 

			@include lg {

				margin: 0 15px;
			}
		}

		div.bg {

			background-repeat: no-repeat;
			background-position: top right;
		}

		div.bg1 {

			background-image: url("../images/block-bg1.png");
		}

		div.bg2 {

			background-image: url("../images/block-bg2.png");
		}

		div.bg3 {

			background-image: url("../images/block-bg3.png");
		}

	}
}

aside {

	width: calc(100% - 40px);
	height: 380px;

	margin: 0 20px;

	@include lg {

		width: 100%;
		margin: 0;
	}

	div.mapButtons {

		width: 100%;
		margin: 0 auto;
		text-align: center;

		@include lg {

			position: absolute;
			width: 1200px;
			margin-left: calc((100% - 1200px) /2;
		}

		div.mapBtn {

			width: 50%;
			height: 40px;
			line-height: 40px;
			text-align: center;
			white-space: nowrap;
			cursor: pointer;
			background: #dcdee3;

			border: 1px solid #c6cbdd;
			display: inline-block;

			position: relative;
			z-index: 99;

			&:hover {

				background: darken(#dcdee3, 4%);
			}

			&.active {

				background: darken(#dcdee3, 4%);
			}

			@include lg {

				width: 195px;
				margin: 0;
				top: 15px;
			}
		}

		div.istButton {

			border-right: 1px solid #c0c0c0;
		}

		div.ankButton {

			border-left: 1px solid #eee;
		}

	}
}

#map {

	height: 100%;
}

footer {

	margin: 0 20px;

	font-size: 16px;
	text-align: center;

	@include lg {

		margin: 0;
	}

	p {

		margin: 50px;
	}
}